.custom-dropdown {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  float: right;
}

.custom-dropdown select {
  color: #999;
  font-size: inherit;
  /*padding: .5em;
  padding-right: 2.5em; */
  padding: 9px 2.5em 9px 10px;
  border: 0;
  margin: 0;
  border-radius: 3px;
  text-indent: 0.01px;
  -o-text-overflow: '';
     text-overflow: '';
  -webkit-appearance: button;
}

.custom-dropdown::before,
.custom-dropdown::after {
  content: "";
  position: absolute;
  pointer-events: none;
}

.custom-dropdown::after {
  content: "\25BC";
  height: 1em;
  font-size: .625em;
  line-height: 1;
  right: 1.2em;
  top: 50%;
  margin-top: -.5em;
}

.custom-dropdown::before {
  width: 2em;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 0 3px 3px 0;
}

.custom-dropdown::before {
  background-color: rgba(0,0,0,.15);
}

.custom-dropdown::after {
  color: rgba(0,0,0,.4);
}

@media screen and (max-width: 600px) {
  .custom-dropdown {
    display: block;
    float: none;
  }
  .custom-dropdown select {
    width: 100%;
  }
}


